.Overlay.Show {
	display: block;
}

/* MODAL */
.Modal {
	position: fixed;
	bottom: -150vh;
	background-color: white;
	color: #100c0c;
	width: 30%;
	box-shadow: 0 0 10px 0px #5a6873;
	left: 70%;
	padding: 0 12px 12px;
	transition: all 0.3s ease-out;
	z-index: 10;
}

.Modal.Show {
	bottom: 0;
	border-radius: 0.2rem;
}

.Close {
	cursor: pointer;
	position: absolute;
	background-color: transparent;
	border: 0;
	font-size: 18px;
	padding: 3px;
}

