/* (3) */
#header-content-div {
	display: flex;
	justify-content: center;
	position: relative;

	/* styling */
	overflow: hidden;
	object-fit: scale-down;
	width: 90%;
	margin-bottom: 1em;
	margin-top: 1em;
	border-radius: 10px;
}

/* (4) */
#header-video {
	object-fit: contain;
	aspect-ratio: 5/3;
	width: 100%;
}

/* (4) */
#header-btn {
	/* styling */
	position: absolute;
	bottom: 5%;
	border-radius: 10rem;
	white-space: nowrap;
	border: 2px dashed white;
	background-color: transparent;
	color: white;
	font-family: 'Sunset Gothic', sans-serif, 'Courier New', Courier, monospace;
	font-size: 1.5em;
	padding: 0.4em;
}

#header-btn span {
	cursor: pointer;
	display: inline-block;
	position: relative;
	transition: 0.5s;
}

#header-btn span:after {
	content: '\BB';
	position: absolute;
	opacity: 0;
	top: 0;
	right: -20px;
	transition: 0.5s;
}

#header-btn:hover span {
	padding-right: 25px;
}

#header-btn:hover span:after {
	opacity: 1;
	right: 0;
}

